export default {
  name: 'Setting',
  props: {
    data: {
      type: Object,
      required: true
    },
    showTitle: {
      type: Boolean,
      required: false,
      default: true
    },
    classes: {
      type: Array,
      required: false,
      default: function () {
        return ['uk-margin']
      }
    }
  },
  computed: {
    selected: {
      get: function () {
        return this.data.currentTextValue()
      },
      set: function (newValue) {
        this.$emit('change', this.data.name, newValue)
      }
    }
  }
}
