export default {
  name: 'WideInflectionsSubTables',
  props: {
    // This will be an InflectionData object
    data: {
      type: [Array],
      required: true
    },
  },

  methods: {
    cellClasses: function (cell) {
      if (cell.role === 'label') { return 'infl-prdgm-tbl-cell--label' }
      if (cell.role === 'data') { return 'infl-prdgm-tbl-cell--data' }
    }
  }
}
